.header {
    // position: fixed;
    // top: 0;
    // left: 0;
    width: 100%;
    background-color: #fff;
    z-index: 10;

    &__container {
        display: flex;
        justify-content: space-between;
        position: relative;
        padding: 40px 20px 20px;
    }

    &__logo-link {
        display: flex;
        align-items: center;
    }

    &__logo-wrap,
    &__hamburger-menu {
        display: flex;
        align-items: center;
    }

    &__hamburger-menu {
        background-color: #fff;
        border-radius: 50%;
        box-shadow: 0 0 5px 2px #fff;
        z-index: 100;
    }

    &__hamburger-menu-icon {
        padding: 5px;
        font-size: 1.6rem;
        color: $color-nautral-grayish-violet;
        cursor: pointer;
    }

    @media screen and (min-width: 1440px) {
        // position: static;

        &__hamburger-menu {
            display: none;
        }
    }
}
